.progress-circle {
  position: relative;
}
.progress-circle circle {
  stroke-width: 8px;
  transform-origin: center;
}
.progress-circle circle.progress-background {
  transform: scale(0.9);
  stroke: rgba(255,205,49,0.5);
}
.progress-circle circle.progress-bar {
  transform: scale(0.9) rotate(-90deg);
  stroke: #ffcd32;
}
